.container__thanks {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 61px 0;
  @include r(520) {
    padding: 32px 0; } }

.wrap__thanks {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 935px;
  @include r(768) {
    flex-direction: column;
    @include r(520) {
      margin: 16px 0; } } }


.wrap__thanks-content {
  max-width: 435px;
  margin-left: 95px;
  @include r(1000) {
    margin-left: 70px;
    @include r(768) {
      margin-left: 0;
      margin-top: 40px;
      @include r(520) {
        margin-top: 25px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column; } } } }




.thanks-content__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 72px;
  color: #7360F2;
  white-space: nowrap;
  @include r(1000) {
    font-size: 40px;
    line-height: 52px;
    @include r(768) {
      font-size: 44px;
      @include r(520) {
        font-size: 24px;
        line-height: 36px; } } } }

.thanks-content__text {
  font-size: 16px;
  line-height: 24px;
  color: #181A22;
  margin: 24px 0;
  max-width: 395px;
  @include r(1000) {
    font-size: 13px;
    line-height: 20px;
    max-width: 320px;
    @include r(768) {
      font-size: 14px;
      max-width: 345px;
      margin: 24px auto;
      @include r(520) {
        font-size: 16px;
        margin: 16px auto;
        max-width: 280px;
        text-align: center; } } } }

.picture__thanks {
  @include r(520) {
    padding: 0 15px; }
  img {
    @include r(1000) {
      width: 320px;
      @include r(768) {
        width: 350px;
        @include r(520) {
          width: 250px; } } } } }

.container__thanks {
  .wrap__thanks {
    .wrap__thanks-content {
      .button {
        --text: #fff;
        --font-size: 22px;
        max-width: 255px;
        @include r(1000) {
          max-width: 190px;
          padding: 12px 28px;
          @include r(768) {
            margin: 0 auto;
            @include r(520) {
              padding: 15px 60px;
              max-width: 288px; } } }
        div {
          span {
            color: #fff; } } }

      .btn-back__error {
        background-color: #7360F2; }


      .btn-back {
        font-size: 14px;
        @include r(520) {
          font-size: 16px; } } } } }
