// прывый слайдер
.gallery-top {
  padding-top: 100px;
  margin-top: -100px;
  border-radius: 0px 8px 8px 8px;
  @include r(900) {
    width: 700px;
    max-width: 700px;
    padding: 0;
    margin-top: -105px;
    @include r(750) {
      width: 630px;
      max-width: 630px;
      @include r(670) {
        width: 375px;
        max-width: 375px;
        border-radius: 0px 0px 8px 8px;
        @include r(520) {
          margin-top: -110px;
          width: 291px;
          max-width: 291px; } } } }
  .gallery-top-wrap {
    .gallery-top-slide {
      background: #FFFFFF;
      border: 1px solid #F2F2F2;
      box-sizing: border-box;
      box-shadow: 0px 4px 20px rgba(39, 38, 46, 0.15);
      border-radius: 0px 8px 8px 8px;
      width: 100%;
      max-width: 825px;
      @include r(900) {
        max-width: 700px;
        @include r(750) {
          max-width: 630px;
          @include r(670) {
            max-width: 375px;
            box-shadow: 0px 4px 20px rgba(39, 38, 46, 0.15);
            border-radius: 0px 0px 8px 8px;
            @include r(520) {
              max-width: 292px; } } } } } }

  .gallery-top-slide-content {
    width: 380px;
    max-width: 380px;
    margin: 33px 62px 41px 49px;
    @include r(1080) {
      margin: 33px 62px 41px 35px;
      @include r(1040) {
        width: 350px;
        max-width: 350px;
        min-height: 450px;
        margin: 33px 62px 7px 30px;
        @include r(670) {
          margin-left: 20px;
          margin-top: 30px;
          max-width: 335px;
          @include r(520) {
            max-width: 255px;
            margin-top: 20px; } } } } }

  .top-slide-wrap-text {
    font-size: 16px;
    line-height: 24px;
    color: #7360F2;
    padding: 12px 15px;
    background-color: #F1EFFE;
    border-radius: 4px;
    margin-bottom: 22px;
    @include r(1040) {
      font-size: 15px;
      line-height: 22px;
      @include r(670) {
        font-size: 16px;
        line-height: 24px;
        color: #4A4A4A;
        @include r(520) {
          margin-bottom: 30px; } } } } }

// слайдер справа
.final-suma-text {
  display: inline-block;
  width: 71px;
  max-width: 71px;
  font-size: 16px;
  line-height: 24px;
  color: #B0B0B0;
  margin-right: 40px;
  margin-top: 25px;
  font-weight: 400;
  margin-left: 10px;
  @include r(1040) {
    margin-right: 15px;
    margin-top: 5px;
    @include r(670) {
      display: block;
      margin: 0;
      margin-left: 10px;
      width: 150px;
      max-width: 150px;
      margin-top: 15px; } } }
.final-suma-number {
  text-align: center;
  width: 250px;
  max-width: 250px;
  margin-bottom: 25px;
  display: inline-block;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #7360F2;
  padding: 7px 55px;
  background-color: #F2F2F2;
  border-radius: 4px;
  @include r(1040) {
    transform: translate(0, -10px);
    @include r(670) {
      transform: translate(0, 0);
      margin-top: 15px;
      margin-left: 10px;
      color: #4a4a4a;
      width: 315px;
      max-width: 315px;
      text-align: center;
      @include r(520) {
        width: 240px;
        padding: 7px 25px; } } } }

// кнопка в слайдере справа
.container__calc {
  form {
    display: flex;
    @include r(900) {
      margin-bottom: 32px; }
    .zayavka {
      border: 1px solid transparent;
      border-radius: 30px;
      height: 52px;
      padding: 0 24px;
      font-size: 16px;
      line-height: 23px;
      color: #4A4A4A;
      transition: all .4s;
      background-color: #FBE75E;
      width: 335px;
      max-width: 335px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      @include r(900) {
        margin-left: auto;
        margin-right: auto;
        @include r(670) {
          max-width: 310px;
          font-size: 16px;
          @include r(520) {
            width: 250px;
            padding: 0 9px;
            font-size: 15px; } } }

      &:hover {
        background-color: #FBE75E; }
      &:focus {
        border: 1px solid transparent; } } }
  .button {
    --text: #7360F2;
    --font-size: 23px;
    @include r(670) {
      --text: #4a4a4a; } }
  .button div span {
    color: #7360F2;
    @include r(670) {
      color: #4a4a4a; } } }

// range

