.container__firstScreen {
  background-image: url('../img/head.jpeg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  height: 93vh;
  position: relative;
  @include center;
  @include r(768) {
    height: 100%;
    display: block; }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(#7360F2, .8); } }


.wrap__firstScreen {
  position: relative;
  transition: opacity .2s;
  @include center;
  flex-direction: column;
  max-width: 625px;
  @include r(768) {
    display: block;
    margin: 0 auto;
    padding: 70px 0; } }

.firstScreen__title {
  text-align: center;
  font-weight: 800;
  font-size: 48px;
  line-height: 72px;
  color: #FFFFFF;
  transition: all 1s;
  @include r(768) {
    font-size: 40px;
    line-height: 65px;
    @include r(620) {
      font-size: 36px;
      line-height: 55px;
      padding: 0 30px;
      @include r(520) {
        font-size: 24px;
        line-height: 36px;
        padding: 0 67px;
        @include r(360) {
          width: 220px;
          margin: 0 auto;
          padding: 0; } } } } }


.firstScreen__text {
  margin-top: 24px;
  margin-bottom: 43px;
  font-size: 18px;
  line-height: 27px;
  color: #FFFFFF;
  text-align: center;
  padding: 0 20px;
  @include r(768) {
    padding: 0px 50px;
    font-size: 16px;
    @include r(520) {
      padding: 0 65px;
      margin-top: 16px;
      margin-bottom: 24px;
      @include r(390) {
        padding: 0px 25px; } } } }






.firstScreen__form {
  form {
    display: flex;
    @include r(620) {
      flex-direction: column; } }
  input[type="tel"], .zayavka1 {
    border: 1px solid transparent;
    border-radius: 30px;
    background-color: #fff;
    height: 52px;
    padding: 0 24px;
    box-sizing: border-box;
    display: block;
    margin-left: auto;
    margin-right: auto;
    font-size: 16px;
    line-height: 23px;
    color: #4A4A4A;
    transition: all .4s;
    &:focus {
      border: 1px solid #00D8FF; }
    &:hover {
      background-color: #F1EFFE; } }
  input::placeholder {
    font-size: 16px;
    line-height: 23px;
    color: rgba(#B0B0B0, .5); }
  .zayavka1 {
    margin-left: 30px;
    background-color: #FBE75E;
    display: flex;
    justify-content: center;
    align-items: center;
    @include r(620) {
      margin-right: auto;
      margin-left: auto;
      margin-top: 16px;
      width: 288px; }
    &:hover {
      background-color: #FBE75E; }
    &:focus {
      border: 1px solid transparent; } }



  input[type="tel"] {
    width: 255px;
    @include r(620) {
      margin: 0 auto;
      width: 288px; } }

  .button {
    --text: #7360F2;
    --font-size: 23px;
    @include r(620) {
      color: #4A4A4A; } }
  .button div span {
    color: #7360F2;
    @include r(620) {
      color: #4A4A4A; } } }




.firstScreen__title .new {
  opacity: 0; }
.firstScreen__title .div_opacity {
  transition: opacity .4s ease-in-out;
  opacity: 1; }

.firstScreen__title-svg {
  position: relative;
  @include r(768) {
    text-align: center; }
  .firstsvg-title1 {
    position: absolute; }
  svg {
    @include r(900) {
      width: 600px;
      height: 109px;
      @include r(768) {
        width: 500px;
        height: 90px;
        @include r(620) {
          width: 380px;
          height: 72px;
          @include r(500) {
            width: 335px;
            height: 64px;
            @include r(370) {
              width: 315px;
              height: 58px; } } } } } } }
